import emModel from '../modal/modal'
import emButton from '../button/button'
import global from '../../mixins/global.js'
export default {
    mixins: [global],
    components:{
        emModel,
        emButton
    },
    props:{
        showModel:{
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            sonShow:false
        };
    },
    watch:{
        showModel(val){
            this.sonShow = val
        }
    },
    methods: {
        cancelModel(){
            this.sonShow = false
            this.$emit('getShow',this.sonShow)
        }
    },
    mounted() {
    },
};
